.navList {
    list-style-type: none;
    margin: auto;
    width: 100%;
    padding: 25px;
    text-align: center;
    background-color: firebrick;
    top: 0;
    color: white;
}

#signImage{
    height: 60px;
    width: 60px;
    float: left;
    margin-top: -15px;
}

li {
    display: inline;
}

.title {
    float: left;
    font-family: 'Viga', sans-serif;
}

.highScore{
    float: right;
    font-family: 'Viga', sans-serif;
}

.currentScore{
    float: right;
    margin-right: 25px;
    font-family: 'Viga', sans-serif;
}

.status{
    float: middle;
    margin: 0 auto;
    font-family: 'Viga', sans-serif;
}

.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
}  
.characterImage {
    width: 200px;
    height: 200px;
}

.cardTitle{
    font-family: 'Permanent Marker', cursive;
}
